import React from "react";
import { Col, Container, Row } from "reactstrap";
import AboutUs from "components/Icons/AboutUs";

interface AProposSection {
  aProposRef: React.MutableRefObject<HTMLDivElement | null>;
}


const AProposSection = ({ aProposRef }: AProposSection) => {
  return (
    <div className="section text-center" ref={aProposRef}>
      <Container>
        <Row>
          <Col className="ml-auto mr-auto" md="12">
            <AboutUs className="icon-about-us" />
            <h1 className="title eureka"> Propos</h1>
            <hr />
          </Col>
        </Row>
        <Row className="section-space-bottom">
          <Col>
            <h4 className="description">
              Eurka Environnement est une firme de gnie-conseil spcialise
              dans la gestion de leau et la conception de systmes de
              traitement. Que vous souhaitiez prlever de leau dun puits, la
              rendre potable, traiter vos eaux uses, grer et rutiliser vos
              eaux pluviales, notre expertise vous permettra de raliser tous
              vos projets. Notre mission : vous offrir un service personnalis
              adapt  vos besoins ainsi quaux enjeux environnementaux actuels
              et futurs. Eurka Environnement, des solutions innovantes  vos
              problmes les plus complexes.
            </h4>
          </Col>
        </Row>
      </Container>
    </div>
  );
};

export default AProposSection;
